import React from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Head from 'next/head';
import { withRouter } from 'next/router';
import '../../styles/main.scss';
import './wrapper.scss';

const routes = [
  '/',
  '/01-about',
  '/02-background',
  '/03-iteration-protocols',
  '/04-iterator',
  '/05-iterable',
  '/06-generator-functions'
];

const Navigation = withRouter((props) => {
  const routeIndex = routes.indexOf(props.router.pathname);
  const prevRoute = routes[routeIndex - 1];
  const PrevComponent = prevRoute ? Link : 'div';
  const nextRoute = routes[routeIndex + 1];
  const NextComponent = nextRoute ? Link : 'div';
  return (
    <div className="navigation">
      <PrevComponent
        href={prevRoute}
      >
        <span
          role="img"
          aria-label="previous slide"
          data-disabled={!prevRoute}
        >
          ðŸ‘ˆ
        </span>
      </PrevComponent>
      &nbsp;
      <NextComponent
        href={nextRoute}
      >
        <span
          role="img"
          aria-label="next-slide"
          data-disabled={!nextRoute}
        >
          ðŸ‘‰
        </span>
      </NextComponent>
    </div>
  );
});

function Wrapper({
  children,
  className
}) {
  return (
    <div>
      <div className={`wrapper ${className}`}>
        {children}
      </div>
      <Navigation />
    </div>
  );
}

Wrapper.propTypes = {
  children: PropTypes.node.isRequired,
  className: PropTypes.string
};

Wrapper.defaultProps = {
  className: ''
};

export default Wrapper;
